import { useContext, useEffect, useRef } from "react"
import { AppState } from "../App"
import { gsap } from "gsap";
import { useLocalStorage } from "../hooks/useLocalStorage";

import "./sideSlider.css"

export default function SideScreen() {
    const [lang, setLang] = useLocalStorage()
    const sideWindow = useRef()
    const state = useContext(AppState)

    const languages = [
        [navigator.language.split("-")[0], "Your language"],
        ["ru", ""],
        ["en", "English"],
        ["be", ""],
        ["uk", ""],
        ["tr", "Trke"],
        ["pl", "Polski"],
        ["ja", ""],
        ["it", "Italiano"],
        ["de", "Deutsch"],
        ["fr", "Franais"]
    ]
    function languageSelect(value, event) {
        event.preventDefault()        
        setLang(value)
        state.dispatch({ type: "change_language",payload:value })
    }

    function switchFilters(screen) {
        switch (screen) {
            case "filter":
                return (
                    <ul className="filter-List">
                        <li onClick={() => state.dispatch({ type: "filter_type", payload: "price" })}
                            className={state.data.filterType === "price" ? "underline" : ""}><span>Price</span></li>
                        <li onClick={() => state.dispatch({ type: "filter_type", payload: "city" })}
                            className={state.data.filterType === "city" ? "underline" : ""}><span >City</span></li>
                        <li onClick={() => state.dispatch({ type: "filter_type", payload: "date" })}
                            className={state.data.filterType === "date" ? "underline" : ""}><span >Date</span></li>
                        <li onClick={() => state.dispatch({ type: "filter_type", payload: "phone" })}
                            className={state.data.filterType === "phone" ? "underline" : ""}><span>Phone</span></li>
                    </ul>
                )
            case "language":
                return (
                    <ul className="filter-List-lang notranslate">
                        {
                            languages.map((element, index) => {
                                return <li key={index}
                                    className={localStorage.getItem("Language") === element[0] ? "underline" : ""}
                                    onClick={(e) => { languageSelect(element[0], e) }}><span>{element[1]}</span></li>
                            })
                        }
                    </ul >
                )
            default:
                console.warn("Error in filter")
        }
    }

    function closeModal(event) {
        event.stopPropagation()
        state.dispatch({ type: "switch_side_window", payload: "closed" })
    }

    useEffect(() => {
        if (state.data.sideWindow === "opened") {
            gsap.to(sideWindow.current, {
                xPercent: -100
            })
        } else {
            gsap.to(sideWindow.current, {
                xPercent: 0
            })
        }
    }, [state.data.sideWindow])

    return (
        <div ref={sideWindow} className="side-screen" onClick={(event) => closeModal(event)}>
            {switchFilters(state.data.filter)}
        </div>
    )
}